@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
* {
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: "Montserrat", sans-serif;
}

.logo {
    width: 60%;
    height: auto;
}

.login {
    display: grid;
    place-items: center;

    height: 100vh;
    background-color: black;
}

.login > a {
    padding: 20px;
    background-color: #1db954;
    border-radius: 99px;
    font-weight: 600;
    color: white;
    text-decoration: none; /* prevent underline from <a> */
    font-family: 'Montserrat', sans-serif;
}
.body {
  /* border: 1px solid red; */
  flex: 0.8 1;
  background: linear-gradient(rgb(91, 87, 115), rgba(0, 0, 0));
  height: 100vh;

  color: white;

  padding: 30px;

  overflow-y: overlay;
}

.body::-webkit-scrollbar {
  display: none;
}

.body__info {
  display: flex;
  align-items: flex-end;
  padding: 10px;
}

.body__infoText {
  flex: 1 1;
}

.body__info > img {
  max-height: 200px;
  margin-right: 20px;
  box-shadow: 0 4px 60px rgba(0, 0, 0, 0.5);
}

.body__infoText > h2 {
  font-size: 68px;
  margin-bottom: 10px;
}

.body__infoText > p {
  font-size: 14px;
  color: whitesmoke;
}

.body__icons {
  display: flex;
  align-items: center;
}

.body__shuffle {
  font-size: 50px !important;
  margin: 20px;
}

.body__shuffle:hover {
  transition: 0.1s ease-in;
  transform: scale(1.08);
}

.body__icons > .MuiSvgIcon-root {
  margin-right: 20px;
}

.body__songs {
  margin: 20px -30px;
}
.header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}

.header__left {
  background-color: white;
  color: grey;
  border-radius: 30px;
  padding: 10px;

  display: flex;
  align-items: center;

  min-width: 80px;
}

.header__left > input {
  border: none;
  width: 100%;
}

.header__right {
  display: flex;
  align-items: center;
}

.header__right > h4{
  margin-left: 10px;
}
.songRow {
  margin-left: 20px;
  padding: 20px;
  display: flex;
  align-items: center;
  color: white;
}

.songRow:hover {
  cursor: pointer;
  background-color: black;
  opacity: 0.8;
}

.songRow__info {
  margin-left: 20px;
  color: white;
}

.songRow__info > h1 {
  font-size: 16px;
}

.songRow__info > p {
  font-size: 14px;
  margin-top: 3px;
  color: grey;
}

.songRow__album {
  height: 40px;
  width: 40px;
}

.footer {
  position: fixed;
  bottom: 0;
  height: 65px;
  width: 100%;
  background-color: #282828;
  padding: 20px;

  display: flex;
  justify-content: space-between;
}

.footer__left {
  flex: 0.3 1;
  color: white;
  display: flex;
  align-items: center;
  width: 300px;
}

.footer__albumLogo {
  height: 60px;
  width: 60px;
  margin-right: 20px;
  object-fit: contain;
}

.footer__songInfo > h4 {
  margin-bottom: 5px;
}

.footer__songInfo > p {
  font-size: 12px;
}

.footer__green {
  color: #1ed15e;
}

.footer__center {
  flex: 0.4 1;
  padding: 0 100px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 300px;
}

.footer__icon:hover, .footer__green:hover {
  transition: transform 0.2s ease-in-out;
  transform: scale(1.2) !important;
}

.footer__right {
  flex: 0.3 1;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;

  max-width: 300px;
  padding-left: 10vw;
}

.footer__right * .MuiSlider-root {
  color: white;
  width: 80%;
  bottom: 2px;
  left: -5px;
}
.player__body {
  display: flex;
}

.sidebar {
  /* border: 1px solid black; */
  height: 100vh;
  flex: 0.2 1;
  background-color: #040404;
  min-width: 230px;

  padding-left: 10px;
  padding-right: 10px;

  /* text color */
  color: white;
}

.sidebar__logo {
  width: 100%;
}

.sidebar__playlistTitle {
    font-size: 12px;
    margin-left: 8px;
    padding: 5px;
}

.sidebar > hr {
    border: 1px solid #282828;
    width: 90%;
    margin: 10px auto;
}
.sidebarOption {
  color: grey;
  height: 40px;
  cursor: pointer;
  transition: 200ms color ease-in;

  display: flex;
  align-items: center;
}

.sidebarOption:hover {
  color: white;
}

.sidebarOption__icon {
  padding-left: 10px;
  padding-right: 10px;
}

/* playlists */
.sidebarOption > p {
  margin-top: 10px;
  margin-left: 20px;
  font-size: 14px;
}

