.body {
  /* border: 1px solid red; */
  flex: 0.8;
  background: linear-gradient(rgb(91, 87, 115), rgba(0, 0, 0));
  height: 100vh;

  color: white;

  padding: 30px;

  overflow-y: overlay;
}

.body::-webkit-scrollbar {
  display: none;
}

.body__info {
  display: flex;
  align-items: flex-end;
  padding: 10px;
}

.body__infoText {
  flex: 1;
}

.body__info > img {
  max-height: 200px;
  margin-right: 20px;
  box-shadow: 0 4px 60px rgba(0, 0, 0, 0.5);
}

.body__infoText > h2 {
  font-size: 68px;
  margin-bottom: 10px;
}

.body__infoText > p {
  font-size: 14px;
  color: whitesmoke;
}

.body__icons {
  display: flex;
  align-items: center;
}

.body__shuffle {
  font-size: 50px !important;
  margin: 20px;
}

.body__shuffle:hover {
  transition: 0.1s ease-in;
  transform: scale(1.08);
}

.body__icons > .MuiSvgIcon-root {
  margin-right: 20px;
}

.body__songs {
  margin: 20px -30px;
}